@import '../../common/styles/saas/variables';

.cardsContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 30px;
  background-color: $bgLightColor;
  min-height: 100vh;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);

  .searchWithAddItem {
    display: flex;
    align-items: center;

    button {
      margin-left: 80px;
    }
  }

  button {
    align-self: flex-start;
    padding: 10px 20px;
    margin-left: 0;

    a {
      text-decoration: none;
      color: #e7e9f3;
    }
  }
}

h1 {
  text-align: center;
  margin-bottom: 30px;
  color: $accentColor;

  span {
    display: block;
  }

  a {
    text-decoration: none;
    color: #e7e9f3;
  }
}
