@import '../../styles/saas/variables';

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  min-height: 80px;

  .navLinks {
    display: flex;
    justify-content: space-between;

    a {
      display: inline-block;
      color: $accentColor;
      font-size: 20px;
      text-decoration: none;
      margin-right: 20px;
      padding: 15px 15px;
      max-height: 50px;
      border-radius: 4px;
      background-color: $bgSecondColor;
      &:hover{
        border-bottom: 6px solid $accentColor;
      }
      &.active {
        color: #fff;
      }
    }
  }

  .devPages {
    position: absolute;
    top: 200px;
    left: 0;
    z-index: 2;
    display: flex;
    flex-direction: column;
    font-size: 14px;
    margin-left: 10px;

    a {
      display: inline-block;
      margin: 5px 0 10px;
    }
  }

  button {
    justify-self: flex-end;
    padding: 10px 15px;
    margin-right: 50px;
    background-color: initial;
    border: 2px solid #a3ece5;
    letter-spacing: initial;
    text-transform: uppercase;
  }
}